.App {
  text-align: center;
  font-style: oblique;
  font-weight: lighter;
}

.header {
  background-color: #161d2b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%;
}

.Tecnologias {
  background-color: rgb(255, 255, 255);
  min-height: 80vh;
  font-size: x-large;
  padding-right: 30%;
  padding-left: 40px;
  padding-top: 25px;
  text-align: left;
  box-sizing: border-box;
  overflow: auto;
  display: block;
}
@media (max-width: 500px) {
  #tecnologias {
    font-size: large;
  }
  .teste {
    width: 100%;
    margin-left: -20px;
  }
  .tabelaDeTecnologias {
    column-count: 1;
  }
}

.Bibliografia {
  background-color: #161d2b;
  min-height: 80vh;
  font-size: x-large;
  padding-right: 3%;
  padding-left: 40px;
  padding-top: 5px;
  text-align: left;
  box-sizing: border-box;
  overflow: auto;
  display: block;
}

#bottom-header {
  position: absolute;
  bottom: 10px;
  left: 10px;
  font-style: italic;
  font-size: medium;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}